/* 
* less global variable
*/
.header-container {
  position: fixed;
  top: 0;
  width: 100%;
  display: grid;
  grid-template-columns: 80px auto;
  justify-items: end;
  background-image: url('../../assets/images/bg5.jpg');
  background-size: 100%;
  z-index: 1;
}
.header-container .name {
  padding: 0 20px;
  color: aliceblue;
  line-height: 48px;
  font-size: 24px;
  z-index: 1;
}
