/* 
* less global variable
*/
.back-content-container {
  height: 100%;
  background-image: url('../../assets/images/bg5.jpg');
  background-size: 100% 100%;
}
.back-content-container .content-container {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate3d(-50%, -50%, 0);
  width: 50%;
  text-align: center;
  color: aliceblue;
}
.back-content-container .content-container h2 {
  color: aliceblue;
  font-size: 24px;
}
.back-content-container .content-container #sentence {
  display: block;
  width: 100%;
  height: 30px;
  font-size: 18px;
  font-family: cursive;
}
.back-content-container .content-container #sentence::after {
  content: '|';
  animation: blink 0.3s infinite;
}
@keyframes blink {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
